/**
 * Utility function for loading.
 * @class AssetUtils
 * @memberof module:jibo/loader
 */
class AssetUtils
{
    /**
     * Checks if the object is plain literal.
     * @method module:jibo/loader.AssetUtils.isPlain
     * @return {Boolean} `true` if it's a plain object.
     */
    static isPlain(obj:any):boolean
    {
        if (typeof obj === 'object' && obj !== null)
        {
            const proto = Object.getPrototypeOf(obj);
            return proto === Object.prototype || proto === null;
        }
        return false;
    }
}

export default AssetUtils;
